import React from 'react'
import ReactDOM from 'react-dom'
import { BrowserRouter, Route, Link } from 'react-router-dom'
import { ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.min.css'
import './index.css'
import Home from './routes/Home'
import AppStoreImage from './assets/img/appstore.png'
import MailImage from './assets/img/mail.png'
import LogoImage from './assets/img/logo.png'
import registerServiceWorker from './registerServiceWorker'

ReactDOM.render(
  <BrowserRouter>
    <div>
      <div className='navbar'>
        <Link to={'/'}>
          <img src={LogoImage} className='logo-img'/>
        </Link>
        <div className='nav-right'>
          <a href={'mailto:press@valetfix.com'} target='_blank'>
            <img src={MailImage} className='mail-img'/>
          </a>
          <a href={'https://itunes.apple.com/us/app/valay-contractor/id1285856800?mt=8'} target='_blank'>
            <img src={AppStoreImage} className='appstore-img'/>
          </a>
        </div>
      </div>
      <Route path='/' component={Home} />
      <ToastContainer toastClassName={'toast-text'}/>
    </div>
  </BrowserRouter>,
document.getElementById('root'))
registerServiceWorker()



